___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  height: 100vh;
  scrollbar-color: white royalblue;
}

form {
  gap: 1rem;
  width: 100%;
  display: flex;
  flex: 0 0 auto;
  padding: 0.5rem;
  overflow: hidden;
  border-radius: 10px;
  flex-direction: column;
}

button {
  border: none;
  cursor: pointer;
  font-size: 1rem;
  max-height: 35px;
  font-weight: bold;
  align-self: center;
  border-radius: 5px;
  padding: 0.5rem 1rem;
}

form .buttons {
  gap: 0.5rem;
  display: flex;
  align-self: flex-end;
}

input,
select {
  font-size: 1rem;
  border: solid 1px;
  border-radius: 5px;
}

#menu {
  overflow: auto;
  color: white;
  padding: 0 1rem;
  flex: 0 0 300px;
  margin-top: 4rem;
  list-style: none;
  background: royalblue;
}

#menu .header-tasks {
  top: 0;
  left: 0;
  gap: 0.5rem;
  display: flex;
  padding: 1rem;
  width: 300px;
  position: fixed;
  background: royalblue;
}

#menu ul {
  gap: 1rem;
  display: flex;
  font-size: 20px;
  margin-top: 1rem;
  overflow: hidden;
  list-style: none;
  font-weight: bold;
  flex-direction: column;
}

#menu li {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

#menu button {
  width: 7rem;
  text-align: start;
}

#menu button:hover {
  color: white;
  background: cornflowerblue;
}

#content {
  gap: 2rem;
  width: 100%;
  display: flex;
  overflow: auto;
  flex-direction: column;
  padding: 2rem 8rem;
}

#content ul {
  gap: 1rem;
  display: flex;
  flex-direction: column;
}

#content li {
  gap: 1rem;
  cursor: pointer;
  display: flex;
  min-height: 5rem;
  max-height: 10rem;
  overflow: auto;
  align-items: start;
  padding: 0.5rem 1rem;
  border-radius: 10px;
  scrollbar-width: none;
}

#content form {
  cursor: default;
}

#content li form {
  gap: 10px;
  padding: 0;
}

#content select:hover,
#content input[type="date"]:hover {
  cursor: pointer;
}

#content input[type="text"]:hover {
  cursor: text;
}

#content li input[type="checkbox"] {
  margin-top: 10px;
  transform: scale(2);
}

#date {
  height: 2rem;
  width: 7rem;
}

#addForm {
  padding: 1rem;
  border: 1px solid royalblue;
}

#addForm .date-priority {
  all: unset;
  gap: .5rem;
  display: flex;
}

#editForm .date-priority {
  all: unset;
  gap: .5rem;
  display: flex;
  justify-content: end;
}

#editForm .buttons {
  all: unset;
  gap: .5rem;
  display: flex;
  padding: 0 3px;
}

#title {
  all: unset;
  font-size: 20px;
  font-weight: bold;
}

#desc {
  all: unset;
  font-size: 18px;
}

.neutral {
  background: lightblue;
  accent-color: lightblue;
}

.low {
  background: lightgreen;
  accent-color: lightgreen;
}

.mid {
  background: orange;
  accent-color: orange;
}

.high {
  background: lightcoral;
  accent-color: lightcoral;
}

.task-details {
  gap: .5rem;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.task-details h4 {
  font-size: 18px;
  align-self: end;
}

.cancel:hover {
  background: darkgray;
}

.add-task,
.plus-add-task {
  color: white;
  background: cornflowerblue;
}

.add-task:hover,
.plus-add-task:hover {
  background: royalblue;
}

@media (max-width: 1000px) {

  body {
    flex-direction: column;
  }

  #menu {
    gap: 6rem;
    display: flex;
    padding: 0 0rem;
    margin-top: 0;
    flex: 0 0 4rem;
    flex-direction: row;
  }

  #menu .header-tasks {
    gap: 0.2rem;
    display: flex;
    padding-top: 1rem;
    width: 0px;
    position: static;
    background: royalblue;
  }

  #menu ul {
    flex-direction: row;
  }

  #menu button {
    width: 2rem;
    text-align: start;
    padding: 0.5rem 0.5rem;

  }

  #content {
    gap: 1rem;
    padding: 1rem 2rem 2rem;
    scrollbar-width: none;
  }

  #date {
    width: 4.2rem;
  }

  #priority {
    width: 2.6rem;
  }

  #editForm .date-priority {
    gap: 0.4rem;
    padding: 0 3px;
  }

  #editForm .buttons {
    gap: 0.4rem;
  }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;EACE,SAAS;EACT,UAAU;EACV,sBAAsB;AACxB;;AAEA;EACE,aAAa;EACb,aAAa;EACb,gCAAgC;AAClC;;AAEA;EACE,SAAS;EACT,WAAW;EACX,aAAa;EACb,cAAc;EACd,eAAe;EACf,gBAAgB;EAChB,mBAAmB;EACnB,sBAAsB;AACxB;;AAEA;EACE,YAAY;EACZ,eAAe;EACf,eAAe;EACf,gBAAgB;EAChB,iBAAiB;EACjB,kBAAkB;EAClB,kBAAkB;EAClB,oBAAoB;AACtB;;AAEA;EACE,WAAW;EACX,aAAa;EACb,oBAAoB;AACtB;;AAEA;;EAEE,eAAe;EACf,iBAAiB;EACjB,kBAAkB;AACpB;;AAEA;EACE,cAAc;EACd,YAAY;EACZ,eAAe;EACf,eAAe;EACf,gBAAgB;EAChB,gBAAgB;EAChB,qBAAqB;AACvB;;AAEA;EACE,MAAM;EACN,OAAO;EACP,WAAW;EACX,aAAa;EACb,aAAa;EACb,YAAY;EACZ,eAAe;EACf,qBAAqB;AACvB;;AAEA;EACE,SAAS;EACT,aAAa;EACb,eAAe;EACf,gBAAgB;EAChB,gBAAgB;EAChB,gBAAgB;EAChB,iBAAiB;EACjB,sBAAsB;AACxB;;AAEA;EACE,gBAAgB;EAChB,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;EACE,WAAW;EACX,iBAAiB;AACnB;;AAEA;EACE,YAAY;EACZ,0BAA0B;AAC5B;;AAEA;EACE,SAAS;EACT,WAAW;EACX,aAAa;EACb,cAAc;EACd,sBAAsB;EACtB,kBAAkB;AACpB;;AAEA;EACE,SAAS;EACT,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,SAAS;EACT,eAAe;EACf,aAAa;EACb,gBAAgB;EAChB,iBAAiB;EACjB,cAAc;EACd,kBAAkB;EAClB,oBAAoB;EACpB,mBAAmB;EACnB,qBAAqB;AACvB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,SAAS;EACT,UAAU;AACZ;;AAEA;;EAEE,eAAe;AACjB;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,gBAAgB;EAChB,mBAAmB;AACrB;;AAEA;EACE,YAAY;EACZ,WAAW;AACb;;AAEA;EACE,aAAa;EACb,2BAA2B;AAC7B;;AAEA;EACE,UAAU;EACV,UAAU;EACV,aAAa;AACf;;AAEA;EACE,UAAU;EACV,UAAU;EACV,aAAa;EACb,oBAAoB;AACtB;;AAEA;EACE,UAAU;EACV,UAAU;EACV,aAAa;EACb,cAAc;AAChB;;AAEA;EACE,UAAU;EACV,eAAe;EACf,iBAAiB;AACnB;;AAEA;EACE,UAAU;EACV,eAAe;AACjB;;AAEA;EACE,qBAAqB;EACrB,uBAAuB;AACzB;;AAEA;EACE,sBAAsB;EACtB,wBAAwB;AAC1B;;AAEA;EACE,kBAAkB;EAClB,oBAAoB;AACtB;;AAEA;EACE,sBAAsB;EACtB,wBAAwB;AAC1B;;AAEA;EACE,UAAU;EACV,WAAW;EACX,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,eAAe;EACf,eAAe;AACjB;;AAEA;EACE,oBAAoB;AACtB;;AAEA;;EAEE,YAAY;EACZ,0BAA0B;AAC5B;;AAEA;;EAEE,qBAAqB;AACvB;;AAEA;;EAEE;IACE,sBAAsB;EACxB;;EAEA;IACE,SAAS;IACT,aAAa;IACb,eAAe;IACf,aAAa;IACb,cAAc;IACd,mBAAmB;EACrB;;EAEA;IACE,WAAW;IACX,aAAa;IACb